import { mapState } from "vuex";

export default {
    data() {
        return {
            haveBackground: false,
            pay_code: '',
            file_path: '',
            expiry_date: '',
            check: null,
            reload: null
        }
    },
    computed: {
        ...mapState({
            userInfo: state => state.user.info,
        })
    },
    onHide() {
        clearInterval(this.check);
        clearInterval(this.reload);
    },
    onUnload() {
        clearInterval(this.check);
        clearInterval(this.reload);
    },
    methods: {
        toBalance() {
            uni.navigateTo({
                url: `/pages/balance/balance`,
            });
        },
        checkTime() {
            let that = this;
            let timestamp = Date.parse(new Date()) /1000;
            let time = (that.expiry_date - timestamp) * 1000;
            this.reload = setTimeout(()=>{
                that.getList();
            },time)
        },
        getCode(pay_code) {
            let that = this;
            that.$request({
                url: that.$api.teller.search,
                data: {
                    pay_code: pay_code
                },
                method: 'post'
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0 && this.userInfo) {
                    if(response.data.is_use) {
                        clearInterval(this.check);
                        clearInterval(this.reload);
                        uni.navigateTo({
                            url: `/plugins/teller/result/result?code=` + pay_code,
                        });
                    }else {
                        this.check = setTimeout(()=>{
                            that.getCode(pay_code);
                        },1000)
                    }
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
        getList() {
            let that = this;
            that.$request({
                url: that.$api.teller.code,
            }).then(response=>{
                that.$hideLoading();
                if(response.code == 0) {
                    that.pay_code = response.data.pay_code;
                    that.file_path = response.data.file_path;
                    that.expiry_date = response.data.expiry_date;
                    that.getCode(response.data.pay_code);
                    that.checkTime();
                }else {
                    uni.showToast({
                        title: response.msg,
                        icon: 'none',
                        duration: 1000
                    });
                }
            }).catch(response => {
                that.$hideLoading();
            });
        },
    },

    onShow(options) {
        this.$showLoading({
            type: 'global',
            text: '加载中...'
        });
        this.getList();
    }
}
